@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;900&display=swap);
*,body{
  font-family: 'Montserrat', sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: #222;
}

img{
  max-width: 100%;
}

.container{
  max-width: 85%;
  margin:auto;
}

/* Navbar */
.nav-block {
  display: flex;
  list-style: none;
}

.home_link{
  border-radius: 5px;
  padding: 0px 10px;
  color: #196B5D;
}


.nav-wrapper{
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  padding: 0 10%;
  align-items: center;
  height: 80px;
  background-color: #222;
}

.nav-block a{
  position: relative;
  margin-left: 30px;
  font-size: 1.2rem;
  margin-top: 10px;
  padding: 5px 10px;
  color: #fff;
}

.nav-block a{
  text-decoration: none;
  display: inline-block;
  font-weight:bold;
  cursor: pointer;
  opacity: .8;
  border-radius: 5px;
}

.link {
  display: inline-block;
  color: #000;
  text-decoration: none;
}

.nav-block a.active{
  position: relative;
  background-color: #196B5D;
}

@media screen and (max-width: 450px) {
  .nav-wrapper a{
    font-size: .62rem;
    margin: 10px 10px 0 0;
    padding: 3px 5px 2px;
  }

  .nav-wrapper{
    padding: 0 1.5%;
  }


}

/* Home */
.home{
  width: 100%;
  height: 100vh;
}

.banner{
  width: 100%;
  height: calc(100vh - 80px);
}

.bn-text{
  display: block;
  z-index: 2;
  width: 700px;
  padding: 20px;
  text-align: center;
  height: 380px;
  position: absolute;
  top: 60%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.heading-div{
  background: rgba(38, 124, 110, 0.9);
  padding: 20px;
  border: 3px solid rgb(25, 107, 93);
  border-radius: 10px;
}

.text-div{
  background: #fafafa;
  padding: 20px;
  border: 3px solid rgb(25, 107, 93);
  border-radius: 0 0 10px 10px;
}

.bn-text p{
  margin: 0;
  margin-bottom: 20px;
  font-size: 1.5rem;
  text-align: left;
}

.heading-small{
  font-size: 2.5rem;
  font-weight: 300;
  color: white;
}

#heading-smaller{
  font-size: 2rem;
}

#heading-smallest{
  font-size: 1.3rem;
}

.heading-big{
  font-size: 3rem;
  color: white;
  margin-top: 10px;

}

.heading-big-2{
  font-size: 2rem;
  color: white;
  margin-top: 10px;
}

.heading-big-3{
  font-size: 2rem;
  color: white;
  margin-top: 10px;

}

.contact-div{
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide-div{
  position: relative;
  width: 100%;
  height: 100%;
}
/* Media query */
@media screen and (max-width: 800px) {
  
}

@media screen and (max-width: 450px) {
  .bn-text{
    width: 100%;
    padding: 0;
  }

  .heading-big{
    font-size: 1.5rem;
  }

  .heading-big-2{
    font-size: 1.5rem;
  }

  .heading-big-3{
    font-size: 1.2rem;
  }

  .heading-small{
    font-size: 1rem;
  }

  #heading-smaller{
    font-size: 1.2rem;
  }

  #heading-smallest{
    font-size: 1.2rem;
  }

}

input[type="text"],input[type="email"], textarea[type="text"] {
  background-color :#fafafa;
}

.input-block{
  position:relative;
  overflow: hidden;
}

.form input{
  width: 100%;
  height: 100%;
  padding-top:20px;
  border: none;
  outline: none;
  color: #222;
  padding-bottom: 5px;
}

.form-label{
  position: absolute;
  bottom: 0;
  left: 0;
  pointer-events: none;
  border-bottom: 1px solid #222;
  width: 100%;
  height: 100%;
}

.form-label::after{
  content:"";
  position: absolute;
  bottom: -1px;
  left: 0;
  border-bottom: 2px solid #000;
  width: 100%;
  height: 100%;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: all 0.3s ease;
}

.label-content{
  position: absolute;
  bottom: 5px;
  left: 0;
  transition: all 0.3s ease;
  color: #222;
}

.form input:focus + .form-label .label-content,
.form input:valid + .form-label .label-content {
  -webkit-transform: translateY(-120%);
          transform: translateY(-120%);
  font-size: 13px;
  color: #000;
}

.form input[value]:not([value=""]) + .form-label .label-content{
  -webkit-transform: translateY(-120%);
          transform: translateY(-120%);
  font-size: 13px;
  color: #000;
}


.form input:focus + .form-label::after,
.form input:valid + .form-label::after{
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.form input[value]:not([value=""]) + .form-label .label-content::after{
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.email {
  width: 50%;
}

/* Contact */


#contact{
  display: grid;
  background-color: #fafafa;
  padding: 30px 0;
  width: 80%;
  z-index: 2;
  box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
}



.connect h1{
  font-size: 3rem;
  padding: 20px 0 0 0;
  color: #222;
  border-bottom: 2px solid #222;
  display: inline-block;
}
.connect{
  margin: 20px 0 60px 0;
}

.name-block{
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
}

.form-block form{
  display: grid;
  grid-gap: 30px;
}


textarea[type="text"]{
  width: 100%;
  height: 250px;
  font-size: 1.2rem;
  padding: 10px;
  margin-top: 30px;
  transition: all 0.6s ease;
}

textarea[type="text"]:focus{
  outline-color: #222;
  box-shadow: 0px 0px 10px 1px #222;
}

#button {
  padding: 10px 20px;
  background: none;
  color: #196B5D;
  margin-left: auto;
  font-weight: bold;
  border-radius: 5px;
  transition: all 0.3s ease;
  cursor: pointer;
  border-color: #196B5D;
}

#button:hover {
  background: #196B5D;
  color: #fff;
}

#msg_sent{
  padding: 10px 20px;
  color: black;
  height: 10px;
}

@media screen and (max-width: 1500px) {
  .connect h1{
    font-size: 2rem;
  }
}

@media screen and (max-width: 450px) {
  .connect{
    width: 100%;
  }
  .connect h1{
    font-size: 6.2vw;
    padding: 0;
  }
  .name-block{
    grid-template-columns: 1fr;
  }
  .social-media{
    width: 60px;
  }
  .social-media a{
    width: 25px;
  }
  .form-block form{
    width: 100%;
  }
  .email{
    width: 100%;
  }
  #button {
    font-size: .75rem;
  }

}

.kontakt-wrapper{
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.kontakt-div{
    padding: 30px;
    background-color: #ddd;
    display: flex;
    justify-content: center;
    padding: 30px 0;

}

.info-wrapper{
    width: 100%;
    height: 100%;
    background: url(/chabi/static/media/trucks.921d4f5c.jpeg);
    display: flex;
    margin: auto;
    background-size: cover;
    position: relative;
}

.info-wrapper::after{
    content: "";
    position: absolute;
    top:0;
    left: 0;
    bottom:0;
    right: 0;
    background: rgba(200, 200, 200, .87);
    z-index: 1;
}

.info-div{
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: auto;
    z-index: 2;
}

.contact-section{
    display: grid;
    grid-gap:30px;
  }
  
  .contact-block-item {
    display: flex;
    grid-template-columns: 1fr 10fr;
    align-items: center;
  }
  .contact-block-item h2 {
   font-size: 1.3rem;
   font-weight: 300;
   margin-left: 10px;
  }
  .icon {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .tr-quote{
     margin-bottom: 30px;
  }

  .tr-quote h2{
    margin-bottom: 15px;
 }

 .icon path{
    fill: #125d7a !important
 }


 @media screen and (max-width: 800px) {
    .kontakt-wrapper{
        grid-template-columns:  1fr;

    }

    .info-wrapper {
        padding: 30px 0;
    }
}

@media screen and (max-width: 450px) {
 
    .contact-block-item h2{
        font-size: 1rem;
    }
}
